/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable consistent-return */
/* eslint-disable react/no-array-index-key */
/* eslint-disable no-use-before-define */
import { useSpring, animated, easings, useSprings } from 'react-spring';
import { useMemo, useState, useEffect, useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import throttle from 'lodash/throttle';
import { createUseGesture, dragAction, wheelAction } from '@use-gesture/react';
import { usePrevious } from '../../hooks/usePrevious';
import { useResponsive } from '../../hooks/useResponsive';
import Style from './Solution.module.scss';

let startY = 0;

const throttleChangeIndex = throttle((action) => action?.(), 1000);

const SolutionTransition = ({ left, right, total, rightExtend }) => {
  useEffect(() => {
    document.body.addEventListener(
      'touchmove',
      (e) => {
        e.preventDefault();
      },
      { passive: false },
    );
  }, []);

  const contentLength = Object.keys(left).length;

  const [transitionIndex, setTransitionIndex] = useState(0);

  const oldTransitionIndex = usePrevious(transitionIndex);

  const { isMobile } = useResponsive();

  const [immediate, setImmediate] = useState(true);

  const getTransformField = useCallback(
    (value, unit = 'px') =>
      isMobile ? `translateX(${value}${unit})` : `translateY(${value}${unit})`,
    [isMobile],
  );

  const history = useHistory();

  useEffect(() => {
    setImmediate(false);
  }, []);

  const handleChangeIndex = useCallback(
    throttle((v) => {
      console.log(111);
      if (v === -1) {
        history.push('/');
        return;
      }

      if (v === total) return;

      setTransitionIndex(v);
    }, 1000),
    [transitionIndex],
  );

  // (v) => {

  //   };

  const getBackgroundColor = (status) => (status ? 'rgba(0, 45, 113, 1)' : 'rgba(0, 45, 113, 0.7)');

  // eslint-disable-next-line arrow-body-style
  const getMaskSpringConfig = ({ status }) => {
    return {
      transform: getTransformField(status ? 200 : -200, isMobile ? 'vw' : 'vh'),
      background: getBackgroundColor(status),
      height: isMobile ? '50vh' : '100vh',
      reset: false,
    };
  };

  const getContentSpringConfig = ({ status, index }) => ({
    transform: getTransformField(status ? 50 : -50),
    opacity: 0,
    reset: false,
    immediate: index !== 0 && immediate,
  });

  const [leftMaskSpring, leftMaskSpringApi] = useSpring(() =>
    getMaskSpringConfig({ status: true }),
  );

  const [rightMaskSpring, rightMaskSpringApi] = useSpring(() =>
    getMaskSpringConfig({ status: false }),
  );

  const [leftContentSprings, leftContentSpringsApi] = useSprings(contentLength, (index) =>
    getContentSpringConfig({ status: true, index }),
  );

  const [rightContentSprings, rightContentSpringsApi] = useSprings(contentLength, (index) =>
    getContentSpringConfig({ status: false, index }),
  );

  const [rightBgSpring, leftBgSpringApi] = useSpring(() => ({
    background: getBackgroundColor(true),
    reset: false,
    immediate,
  }));

  const [leftBgSpring, rightBgSpringApi] = useSpring(() => ({
    background: getBackgroundColor(false),
    reset: false,
    immediate,
  }));

  const getScreenUnit = useCallback(() => (isMobile ? 'vw' : 'vh'), [isMobile]);

  const startMaskTransition = useCallback(
    ({ delay } = { delay: 500 }) => {
      const isNext = oldTransitionIndex < transitionIndex;

      const commonConf = {
        immediate: false,
      };

      leftMaskSpringApi.start(() => {
        const handleResolve = (status) => {
          leftBgSpringApi.start(() => ({
            background: getBackgroundColor(status),
          }));
        };

        if (isNext) {
          return {
            transform: getTransformField(-100, getScreenUnit()),
            ...getMaskConfig(isNext, { delay, duration: 800 }),
            ...commonConf,
            onResolve: () => handleResolve(transitionIndex % 2 === 0),
          };
        }

        return {
          transform: getTransformField(100, getScreenUnit()),
          ...getMaskConfig(isNext, { delay, duration: 800 }),
          ...commonConf,
          onResolve: () => handleResolve(transitionIndex % 2 === 0),
        };
      });

      rightMaskSpringApi.start(() => {
        const handleResolve = (status) => {
          rightBgSpringApi.start(() => ({
            background: getBackgroundColor(status),
          }));
        };

        if (isNext) {
          return {
            transform: getTransformField(100, getScreenUnit()),
            ...getMaskConfig(isNext, { delay, duration: 800 }),
            ...commonConf,
            onResolve: () => handleResolve(transitionIndex % 2 !== 0),
          };
        }

        return {
          transform: getTransformField(-100, getScreenUnit()),
          ...getMaskConfig(isNext, { delay, duration: 800 }),
          onResolve: () => handleResolve(transitionIndex % 2 !== 0),
          ...commonConf,
        };
      });
    },
    [
      getScreenUnit,
      getTransformField,
      leftBgSpringApi,
      leftMaskSpringApi,
      oldTransitionIndex,
      rightBgSpringApi,
      rightMaskSpringApi,
      transitionIndex,
    ],
  );

  const startContentTransition = useCallback(
    ({ delay } = { delay: 800 }) => {
      leftContentSpringsApi.start((index) => {
        const isNext = oldTransitionIndex < transitionIndex;

        if (index === oldTransitionIndex && index !== transitionIndex) {
          return {
            transform: getTransformField(isNext ? -50 : 50),
            opacity: 0,
            ...getContentConfig(isNext),
          };
        }

        if (index === transitionIndex) {
          return {
            transform: getTransformField(0),
            opacity: 1,
            delay,
            ...getContentConfig(isNext),
          };
        }
      });

      rightContentSpringsApi.start((index) => {
        const isNext = oldTransitionIndex < transitionIndex;

        if (index === oldTransitionIndex && index !== transitionIndex) {
          return {
            transform: getTransformField(isNext ? 50 : -50),
            opacity: 0,
            ...getContentConfig(isNext),
          };
        }

        if (index === transitionIndex) {
          return {
            transform: getTransformField(0),
            opacity: 1,
            delay,
            ...getContentConfig(isNext),
          };
        }
      });
    },
    [
      getTransformField,
      leftContentSpringsApi,
      oldTransitionIndex,
      rightContentSpringsApi,
      transitionIndex,
    ],
  );

  const preHandle = useCallback(
    (onReady) => {
      const isNext = oldTransitionIndex < transitionIndex;

      leftMaskSpringApi.start(() => ({
        transform: getTransformField(isNext ? 100 : -100, getScreenUnit()),
        background: getBackgroundColor(transitionIndex % 2 === 0),
        config: {
          duration: 0,
          delay: 0,
        },
        immediate: true,
        onResolve() {
          onReady?.();
        },
      }));

      rightMaskSpringApi.start(() => ({
        transform: getTransformField(isNext ? -100 : 100, getScreenUnit()),
        background: getBackgroundColor(transitionIndex % 2 !== 0),
        config: {
          duration: 0,
          delay: 0,
        },
        immediate: true,
      }));
    },
    [
      getScreenUnit,
      getTransformField,
      leftMaskSpringApi,
      oldTransitionIndex,
      rightMaskSpringApi,
      transitionIndex,
    ],
  );

  useEffect(() => {
    preHandle(() => {
      startMaskTransition({ delay: 300 });
      startContentTransition({ delay: 800 });
    });
  }, [
    getTransformField,
    isMobile,
    leftContentSpringsApi,
    leftMaskSpringApi,
    oldTransitionIndex,
    preHandle,
    rightContentSpringsApi,
    rightMaskSpringApi,
    startContentTransition,
    startMaskTransition,
    transitionIndex,
  ]);

  const getContentConfig = (isNext) => ({
    config: {
      mass: 1,
      easing: isNext ? easings.easeInSine : easings.easeOutSine,
    },
  });

  const getMaskConfig = (isNext, extend = {}) => ({
    config: {
      mass: 1,
      easing: isNext ? easings.easeInSine : easings.easeOutSine,
      ...extend,
    },
  });

  const manualTransition = ({ distance }) => {
    leftContentSpringsApi.start((index) => {
      const isNext = oldTransitionIndex < transitionIndex;
      const rate = distance / window.innerHeight;
      const value = isNext ? -50 * rate : 50 * rate;

      if (index === transitionIndex) {
        return {
          transform: getTransformField(value),
          opacity: 1 * (1 - rate),
          ...getContentConfig(isNext),
        };
      }
    });

    rightContentSpringsApi.start((index) => {
      const isNext = oldTransitionIndex < transitionIndex;

      const rate = distance / window.innerHeight;

      const value = isNext ? 50 * rate : -50 * rate;

      if (index === transitionIndex) {
        return {
          transform: getTransformField(value),
          opacity: 1 * (1 - rate),
          ...getContentConfig(isNext),
        };
      }
    });
  };

  const useGesture = createUseGesture([dragAction, wheelAction]);

  const bind = useGesture(
    {
      onWheelStart({ movement: [, my] }) {
        startY = my;
      },
      // onWheel({ down, movement: [, my] }) {
      //   // if (Math.abs(my - startY) >= 300) {
      //   //   handleChangeIndex(my < startY ? transitionIndex - 1 : transitionIndex + 1);
      //   // }
      //   // console.log('onWheel', down, my);
      // },
      onWheelEnd({ movement: [, my] }) {
        if (Math.abs(my - startY) >= 300) {
          handleChangeIndex(my < startY ? transitionIndex - 1 : transitionIndex + 1);
        }
      },
      onDrag({ down, movement: [, my] }) {
        manualTransition({ distance: my });
        startY = my;
        console.log(down, my);
      },
      onDragStart: ({ movement: [, my] }) => {
        if (!startY) {
          startY = my;
        }
      },
      onDragEnd: ({ movement: [, my] }) => {
        startY = 0;

        if (Math.abs(my - startY) >= 100) {
          handleChangeIndex(my < startY ? transitionIndex + 1 : transitionIndex - 1);
        }
      },
    },
    { drag: {} },
  );

  return (
    <animated.div {...bind()} className={Style.TransitionWarp}>
      <animated.div style={leftBgSpring} className={Style.Left}>
        <animated.div
          style={{ transform: leftMaskSpring.transform, height: leftMaskSpring.height }}
          className={Style.LeftMask}
        />

        {leftContentSprings.map(({ transform, opacity }, i) => (
          <div
            className={Style.ContentWarp}
            style={{ zIndex: i === transitionIndex ? 9 : 9 - i }}
            key={`m-${i}`}>
            <animated.div
              style={{
                transform,
                opacity,
              }}
              key={i}
              className={Style.LeftContent}>
              {left[i]}
              {transitionIndex !== total - 1 && (
                <div
                  className={Style.NextBtn}
                  onClick={() => handleChangeIndex(transitionIndex + 1)}>
                  <img src="/static/solution/nextArrow.svg" alt="foundUs" />
                </div>
              )}
            </animated.div>
          </div>
        ))}
      </animated.div>

      <animated.div style={rightBgSpring} className={Style.Right}>
        <animated.div
          style={{
            transform: rightMaskSpring.transform,
            height: rightMaskSpring.height,
          }}
          className={Style.RightMask}
        />

        {rightContentSprings.map((springConf, i) => (
          <div
            className={Style.ContentWarp}
            style={{ zIndex: i === transitionIndex ? 9 : 9 - i }}
            key={`m-${i}`}>
            <animated.div style={springConf} key={i} className={Style.RightContent}>
              <div className={Style.BackBtn} onClick={() => handleChangeIndex(transitionIndex - 1)}>
                <img src="/static/solution/prevArrow.svg" alt="foundUs" /> back
              </div>

              {right[i]}
            </animated.div>
          </div>
        ))}

        {rightExtend?.(transitionIndex)}
      </animated.div>

      <div className={Style.Progress}>
        <div
          style={{
            width: `${100 * ((transitionIndex + 1) / total)}%`,
          }}
          className={Style.ProgressColor}
        />
      </div>
    </animated.div>
  );
};

export default SolutionTransition;
