import { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { useSpring, animated, easings } from 'react-spring';
import classnames from 'classnames';
import { menu } from './constants';
import Style from './Style.module.scss';

const width = 100;

const Header = () => {
  const [hasMenuOpen, setHasMenuOpen] = useState(false);

  const [immediate, setImmediate] = useState(true);

  useEffect(() => {
    setImmediate(false);
  }, []);

  const [menuSpring, menuSpringApi] = useSpring(() => ({
    transform: 'translateX(-30px)',
    config: {
      mass: 1,
      duration: 200,
    },
    immediate,
    reset: false,
  }));

  const [spring, springApi] = useSpring(() => ({
    width: `${width}vw`,
    transform: `translateX(${width}vw)`,
    background: 'rgba(0, 45, 113, 1)',
    immediate,
    reset: false,
  }));

  useEffect(() => {
    if (hasMenuOpen) {
      springApi.start(() => ({
        transform: `translateX(0vw)`,
        background: 'rgba(0, 45, 113, 0.9)',
        config: {
          clamp: false,
          easing: easings.easeInSine,
        },
      }));

      menuSpringApi.start(() => ({
        transform: `translateX(0px)`,
        delay: 200,
        config: {
          clamp: false,
          easing: easings.easeInSine,
        },
      }));
    } else {
      springApi.start(() => ({
        transform: `translateX(${width}vw)`,
        background: 'rgba(0, 45, 113, 0.9)',
        delay: 100,
        config: {
          clamp: false,
          easing: easings.easeOutSine,
        },
      }));

      menuSpringApi.start(() => ({
        transform: `translateX(30px)`,
        config: {
          clamp: false,
          easing: easings.easeInSine,
        },
      }));
    }
  }, [hasMenuOpen]);

  const handleMenuButtonClick = () => {
    setHasMenuOpen(!hasMenuOpen);
  };

  return (
    <div className={Style.Header}>
      <div className={Style.HeaderLogo}>
        <NavLink to="/">
          <img className={Style.HeaderLogoImage} src="/static/icons/logo.svg" alt="eleatek" />
          <img className={Style.HeaderLogoText} src="/static/icons/logoText.svg" alt="eleatek" />
        </NavLink>
      </div>
      <div className={Style.HeaderMenu}>
        {/* <div className={Style.HeaderMenuItem} onClick={() => handleMenuButtonClick()}>
          MENU
        </div> */}
        <div
          className={classnames(Style.Hamburger, { [Style.HamburgerActive]: hasMenuOpen })}
          onClick={() => handleMenuButtonClick()}>
          <span className={Style.HamburgerLine} />
          <span className={Style.HamburgerLine} />
          <span className={Style.HamburgerLine} />
        </div>
        {/* <div className={Style.HeaderMenuItem}>STATISTICS</div> */}
      </div>

      <animated.div style={spring} className={Style.MenuWarp}>
        {/* <div className={Style.MenuLabel}>MENU</div> */}
        <animated.div style={menuSpring}>
          {menu.map((menuItem) => (
            <div
              className={Style.MenuItem}
              key={menuItem.path}
              onClick={() => handleMenuButtonClick()}>
              <NavLink to={menuItem.path}>{menuItem.name}</NavLink>

              {menuItem.children?.map((childMenuItem) => (
                <div
                  className={Style.ChildMenuItem}
                  key={childMenuItem.path}
                  onClick={() => handleMenuButtonClick()}>
                  <NavLink to={childMenuItem.path}>{childMenuItem.name}</NavLink>
                </div>
              ))}
            </div>
          ))}
        </animated.div>
      </animated.div>
    </div>
  );
};

export default Header;
